@import '~assets/styles/h3Imports';

@mixin hover-and-pressed-light {
  &:not([data-disabled='true'])[data-mode='light'] {
    &:hover {
      background-color: var(--text-button-hover-background);
    }

    &:active {
      background-color: var(--text-button-pressed-background);
    }

    &[data-style='primary'] {
      color: var(--text-button-color);

      --icon-stroke: var(--text-button-color);
    }

    &[data-style='delete'] {
      color: var(--text-button-delete-color);

      --icon-stroke: var(--text-button-delete-color);

      &:active {
        color: var(--text-button-delete-color-pressed);

        --icon-stroke: var(--text-button-delete-color-pressed);
      }
    }
  }
}

@mixin hover-and-pressed-dark {
  &:not([data-disabled='true'])[data-mode='dark'] {
    color: var(--dark-text-button-color);

    --icon-stroke: var(--dark-text-button-color);

    &:hover {
      background-color: var(--dark-text-button-hover-background);
    }

    &:active {
      background-color: var(--dark-text-button-pressed-background);
    }

    &[data-style='delete'] {
      color: var(--text-button-delete-color);

      --icon-stroke: var(--text-button-delete-color);

      &:active {
        color: var(--text-button-delete-color-pressed);

        --icon-stroke: var(--text-button-delete-color-pressed);
      }
    }
  }
}

@mixin hover-and-pressed {
  transition: background-color 0.5s, color 0.5s;
  @include hover-and-pressed-light;
  @include hover-and-pressed-dark;
}

.text-button {
  background-color: transparent;
  border: none;
  border-radius: 8px;
  color: var(--text-button-color);
  display: inline-flex;
  justify-content: center;
  min-height: 20px;
  min-width: 92px;
  padding: var(--spacing-xxxs);
  text-decoration: none;
  @include hover-and-pressed;

  &:focus-visible {
    border-radius: 8px;
    outline: 2px solid var(--text-button-outline);
  }

  .text {
    align-items: center;
    display: inline-flex;
    height: 24px;
    margin: 0;
  }

  &[data-place='none'] {
    @include button-text;
  }

  &[data-place='none-small'] {
    @include button-text-small;
  }

  &[data-place='left'],
  &[data-place='right'] {
    @include interactive-text;

    align-items: center;
    display: inline-flex;

    :nth-child(1) {
      height: rem(24);
      width: rem(24);
    }
  }

  &[data-place='left'] {
    :nth-child(1) {
      margin-right: 8px;
    }
  }

  &[data-place='right'] {
    :nth-child(1) {
      margin-left: 4px;
      order: 1;
    }
  }

  &[data-place='top'] {
    @include interactive-text;

    display: inline-block;
    min-height: 52px;
    min-width: 38px;

    :nth-child(1) {
      display: flex;
      height: 24px;
      justify-content: center;
      margin-bottom: 5px;
    }
  }

  &[data-disabled='true'] {
    color: var(--disabled-button-background);
    pointer-events: none;

    --icon-stroke: var(--disabled-button-background);

    &[data-mode='dark'] {
      color: var(--dark-disabled-text-button-background, #adadc1);

      --icon-stroke: var(--dark-disabled-text-button-background, #adadc1);
    }

    &:focus-visible {
      outline: none;
    }
  }

  span svg rect {
    fill: none;
  }
}
